export default function() {
  return `<table class="signature" cellspacing="0" cellpadding="0" border="0" width="130px">
      <tr>
          <td>
              <p style="margin-top:10px;">${ this.props.greeting }</p>
              <p style="margin:0; font-weight:bold;">${ this.props.name }</p>
              <p style="margin:0; color:#4db5b7; font-weight:bold;">${this.props.position}</p>
              <a href="http://www.scoutgaminggroup.com"><img alt="Scout Gaming Group" src="http://cloud.globalit.no/index.php/s/RoalM2VMklj90rk/download" width="120" style="border:0; left-align:top; margin:4px 0px; display:block;"/></a>
              <hr style='color:red'/>
              <p style="margin:0; font-size:11px;">${ this.props.company }</p>
              <p style="margin:0; font-size:11px;">${ this.props.address }</p>
              <p style="margin:0; font-size:11px;">${ this.props.city }</p>
              <p style="margin:0;"><a href="mailto:${this.props.email}" style="margin:0; padding:0; font-size:11px; color:#4cb5b7; font-family:Arial; font-weight:normal; text-decoration:none; ">${ this.props.email }</a></p>
              <p style="margin:0;"><a href="tel:${this.props.phone}" style="margin:0; padding:0; font-size:11px; color:#4cb5b7; font-family:Arial; font-weight:normal; text-decoration:none; ">${ this.props.phone }</a></p>
              <p style="margin:0;"><a href="http://www.scoutgaminggroup.com" style="margin:0; padding:0; font-size:11px; color:#4db5b7; font-family:Arial; font-weight:normal; text-decoration:none; ">www.scoutgaminggroup.com</a></p>
          </td>
      </tr>
  </table>
  `
}



