import React, { Component } from 'react';
import htmlText from './html-signature'
import Clipboard from 'clipboard'
import './signature.css';

class Signature extends Component {
  constructor(props) {
    super(props)
  }
  copy() {
    const element = document.querySelector('#clipboard')
    element.select()
    document.execCommand("copy")
    this.$copying = true
    this.forceUpdate()
    setTimeout(()=> {
      this.$copying = false
      this.forceUpdate()
    }, 2000)
  }
  htmlData() {
    return htmlText.bind(this)
  }
  render() {
    return (
      <div className="Signature">
      <div className="preview" dangerouslySetInnerHTML={{
        __html: htmlText.bind(this)()
      }}></div>
      <textarea id="clipboard" value={ htmlText.bind(this)() }></textarea>
      <button className={ this.$copying ? 'copied' : '' } onClick={ this.copy.bind(this) }>
        {this.$copying ? <span>👏</span> : <span>💩</span> }
        {this.$copying ? 'Copied!' : 'Copy'}
      </button>
      </div>
    )
  }
}

export default Signature;



