import React, { Component } from 'react'
import Signature from './components/signature/signature'
import htmlText from './components/signature/html-signature'
import Clipboard from 'clipboard'

import './App.css';

class App extends Component {
  constructor() {
    super()
    this.state = {
      name: '',
      email: '',
      phone: '',
      position: '',
      greeting: 'Best regards,',
      company: localStorage.company || 'Scout Gaming Tech AS',
      address: localStorage.address || 'Kanalveien 64',
      city: localStorage.city || '5064 Bergen, Norway',
    }
    this.setVal = this.setVal.bind(this)
  }

  htmlData() {
    return htmlText.bind(this)()
  }

  setVal(e) {
    const name = e.target.name
    const value = e.target.value
    this.setState(()=> {
      const advanced = ['company', 'address', 'city']
      if(advanced.includes(name)) localStorage.setItem(name, value)
      const obj = {}
      obj[name] = value
      return obj
    })
    this.state[e.target.name] = e.target.value
    this.forceUpdate()
  }

  toggleAdvanced() {
    this.setState((prevState)=> {
      return {showAdvanced: !prevState.showAdvanced}
    })

  }

  render() {
    const advancedData = () => {
      if(!this.state.showAdvanced) {
        return (
          <div className="link" onClick={ this.toggleAdvanced.bind(this) }>
            Show advanced
          </div>
        )
      }
      return (
        <div className="advanced">
          <div>
            <div className="label">Company:</div>
              <input
                name="company"
                type="text"
                value={ this.state.company }
                onChange={ this.setVal }
              />
          </div>
          <div>
            <div className="label">Address:</div>
              <input
                name="address"
                type="text"
                value={ this.state.address }
                onChange={ this.setVal }
              />
          </div>
          <div>
            <div className="label">City:</div>
              <input
                name="city"
                type="text"
                value={ this.state.city }
                onChange={ this.setVal }
              />
          </div>
          <div>
            <div className="label">Greeting:</div>
              <input
                name="greeting"
                type="text"
                value={ this.state.greeting }
                onChange={ this.setVal }
              />
          </div>
          <div className="link" onClick={ this.toggleAdvanced.bind(this) }>
            Hide advanced
          </div>
        </div>
      )
    }

    return (
      <div className="App">
        <div className="header">
          <img className="logo" src="http://cloud.globalit.no/index.php/s/RoalM2VMklj90rk/download" />
          e-mail signature generator
        </div>
        <div className="row">
          <div className="form">
            <div>
              <div className="label">Name:</div>
              <input
                name="name"
                value={ this.state.name }
                onChange={ this.setVal }
              />
            </div>

            <div>
              <div className="label">Phone:</div>
                <input
                  name="phone"
                  type="tel"
                  value={ this.state.phone }
                  onChange={ this.setVal }
                />
            </div>

            <div>
              <div className="label">Email:</div>
                <input
                  name="email"
                  type="email"
                  value={ this.state.email }
                  onChange={ this.setVal }
                />
            </div>
            <div>
              <div className="label">Position:</div>
                <input
                  name="position"
                  type="text"
                  value={ this.state.position }
                  onChange={ this.setVal }
                />
            </div>
            { advancedData.bind(this)() }
          </div>
          <Signature
            name={ this.state.name }
            phone={ this.state.phone }
            email={ this.state.email }
            position= { this.state.position }
            company= { this.state.company }
            address= { this.state.address }
            city= { this.state.city }
            greeting= { this.state.greeting }
          />
        </div>
      </div>
    );
  }
}

export default App;



